"use client";

import { useState } from "react";
import { Button } from "@/components/ui/button";
import { Card } from "@/components/ui/card";
import { Input } from "@/components/ui/input";
import { Select } from "@/components/ui/select";

interface BookingFormProps {
  onSubmit: (bookingData: BookingData) => void;
}

export interface BookingData {
  facilityId: string;
  date: string;
  startTime: string;
  duration: number;
  paymentMethod: "syriatel" | "mtn" | "bemo" | null;
}

export default function BookingForm({ onSubmit }: BookingFormProps) {
  const [bookingData, setBookingData] = useState<BookingData>({
    facilityId: "",
    date: "",
    startTime: "",
    duration: 30,
    paymentMethod: null,
  });

  const [error, setError] = useState<string | null>(null);

  const validateBooking = () => {
    // Reset error
    setError(null);

    // Validate minimum duration (30 minutes)
    if (bookingData.duration < 30) {
      setError("Minimum booking duration is 30 minutes");
      return false;
    }

    // Validate maximum duration (2 hours) except for GYM
    if (bookingData.duration > 120 && bookingData.facilityId !== "gym") {
      setError("Maximum booking duration is 2 hours");
      return false;
    }

    // Validate date is not in the past
    const selectedDate = new Date(bookingData.date + "T" + bookingData.startTime);
    if (selectedDate < new Date()) {
      setError("Cannot book for past dates");
      return false;
    }

    // Validate payment method is selected
    if (!bookingData.paymentMethod) {
      setError("Please select a payment method");
      return false;
    }

    return true;
  };

  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault();
    
    if (validateBooking()) {
      onSubmit(bookingData);
    }
  };

  return (
    <Card className="bg-zinc-900 border-0 p-8">
      <form onSubmit={handleSubmit} className="space-y-6">
        {error && (
          <div className="bg-red-500/10 border border-red-500 text-red-500 px-4 py-2 rounded">
            {error}
          </div>
        )}

        <div>
          <label className="block text-sm font-medium mb-2 text-gray-400">Facility</label>
          <select
            value={bookingData.facilityId}
            onChange={(e) => setBookingData({ ...bookingData, facilityId: e.target.value })}
            className="w-full bg-zinc-800 border-zinc-700 text-white rounded-md px-3 py-2"
            required
          >
            <option value="">Select a facility</option>
            <option value="gym">Gymnasium</option>
            <option value="tennis">Tennis Court</option>
            <option value="football">Football Field</option>
          </select>
        </div>

        <div>
          <label className="block text-sm font-medium mb-2 text-gray-400">Date</label>
          <Input
            type="date"
            value={bookingData.date}
            onChange={(e) => setBookingData({ ...bookingData, date: e.target.value })}
            className="bg-zinc-800 border-zinc-700 text-white"
            required
            min={new Date().toISOString().split("T")[0]}
          />
        </div>

        <div>
          <label className="block text-sm font-medium mb-2 text-gray-400">Start Time</label>
          <Input
            type="time"
            value={bookingData.startTime}
            onChange={(e) => setBookingData({ ...bookingData, startTime: e.target.value })}
            className="bg-zinc-800 border-zinc-700 text-white"
            required
            step="1800" // 30-minute intervals
          />
        </div>

        <div>
          <label className="block text-sm font-medium mb-2 text-gray-400">Duration (minutes)</label>
          <Input
            type="number"
            value={bookingData.duration}
            onChange={(e) => setBookingData({ ...bookingData, duration: parseInt(e.target.value) })}
            className="bg-zinc-800 border-zinc-700 text-white"
            required
            min="30"
            max={bookingData.facilityId === "gym" ? "240" : "120"}
            step="30"
          />
        </div>

        <div>
          <label className="block text-sm font-medium mb-2 text-gray-400">Payment Method</label>
          <select
            value={bookingData.paymentMethod || ""}
            onChange={(e) => setBookingData({ 
              ...bookingData, 
              paymentMethod: e.target.value as BookingData["paymentMethod"]
            })}
            className="w-full bg-zinc-800 border-zinc-700 text-white rounded-md px-3 py-2"
            required
          >
            <option value="">Select payment method</option>
            <option value="syriatel">Syriatel Cash</option>
            <option value="mtn">MTN Cash</option>
            <option value="bemo">Bemo Bank</option>
          </select>
        </div>

        <Button 
          type="submit"
          className="w-full bg-gradient-to-r from-blue-500 to-indigo-600 text-white hover:from-blue-600 hover:to-indigo-700 transition-all duration-300"
        >
          Confirm Booking
        </Button>
      </form>
    </Card>
  );
}
