"use client";

import { useState } from "react";
import { useAuth } from "@/context/AuthContext";
import { useRouter } from "next/navigation";
import BookingForm, { BookingData } from "@/components/BookingForm";
import { Card } from "@/components/ui/card";

type DiscountRule = {
  name: string;
  condition: (value: string | number) => boolean;
  discount: number;
};

type DiscountRules = {
  [key: string]: DiscountRule;
};

export default function BookingPage() {
  const { userRole } = useAuth();
  const router = useRouter();
  const [bookingStatus, setBookingStatus] = useState<"idle" | "processing" | "success" | "error">("idle");
  const [statusMessage, setStatusMessage] = useState("");

  // Simulated facility prices (in real app, these would come from an API/database)
  const facilityPrices = {
    gym: 50,      // per hour
    tennis: 80,   // per hour
    football: 120 // per hour
  };

  // Simulated discount rules (in real app, these would come from an API/database)
  const discounts: DiscountRules = {
    earlyBird: {
      name: "Early Bird",
      condition: (time: string | number) => {
        if (typeof time === 'string') {
          return parseInt(time.split(":")[0]) < 10;
        }
        return false;
      },
      discount: 0.15
    },
    offPeak: {
      name: "Off-Peak Hours",
      condition: (time: string | number) => {
        if (typeof time === 'string') {
          const hour = parseInt(time.split(":")[0]);
          return hour > 13 && hour < 16;
        }
        return false;
      },
      discount: 0.10
    },
    longDuration: {
      name: "Long Duration",
      condition: (duration: string | number) => {
        if (typeof duration === 'number') {
          return duration >= 120;
        }
        return false;
      },
      discount: 0.20
    }
  };

  const calculatePrice = (bookingData: BookingData) => {
    const facility = bookingData.facilityId as keyof typeof facilityPrices;
    const basePrice = facilityPrices[facility] * (bookingData.duration / 60);
    
    // Calculate applicable discounts
    let maxDiscount = 0;
    let appliedDiscountName = "";

    // Check early bird and off-peak discounts with start time
    if (discounts.earlyBird.condition(bookingData.startTime)) {
      maxDiscount = discounts.earlyBird.discount;
      appliedDiscountName = discounts.earlyBird.name;
    }
    if (discounts.offPeak.condition(bookingData.startTime)) {
      if (discounts.offPeak.discount > maxDiscount) {
        maxDiscount = discounts.offPeak.discount;
        appliedDiscountName = discounts.offPeak.name;
      }
    }

    // Check long duration discount
    if (discounts.longDuration.condition(bookingData.duration)) {
      if (discounts.longDuration.discount > maxDiscount) {
        maxDiscount = discounts.longDuration.discount;
        appliedDiscountName = discounts.longDuration.name;
      }
    }

    const finalPrice = basePrice * (1 - maxDiscount);
    return {
      basePrice,
      discount: maxDiscount,
      discountName: appliedDiscountName,
      finalPrice
    };
  };

  const handleBooking = async (bookingData: BookingData) => {
    setBookingStatus("processing");
    setStatusMessage("Processing your booking...");

    try {
      // Calculate price with any applicable discounts
      const pricing = calculatePrice(bookingData);

      // In a real app, this would be an API call to process the payment and create the booking
      await new Promise(resolve => setTimeout(resolve, 2000)); // Simulate API call

      // Simulate successful booking
      setBookingStatus("success");
      setStatusMessage(`Booking confirmed! ${
        pricing.discountName 
          ? `\n${pricing.discountName} discount applied: ${(pricing.discount * 100).toFixed(0)}% off`
          : ""
      }\nTotal price: $${pricing.finalPrice.toFixed(2)}`);

      // Redirect to dashboard after 3 seconds
      setTimeout(() => {
        router.push("/dashboard");
      }, 3000);

    } catch (error) {
      setBookingStatus("error");
      setStatusMessage("Failed to process booking. Please try again.");
    }
  };

  // Protect the booking page
  if (!userRole) {
    router.push("/auth/login");
    return null;
  }

  return (
    <div className="min-h-screen bg-black text-white p-8 pt-24">
      <div className="max-w-3xl mx-auto">
        <h1 className="text-3xl font-bold mb-8 bg-gradient-to-r from-blue-500 to-indigo-600 bg-clip-text text-transparent">
          Book a Facility
        </h1>

        {bookingStatus === "success" ? (
          <Card className="bg-zinc-900 border-0 p-8">
            <div className="text-center">
              <div className="text-green-400 text-xl mb-4">âœ“</div>
              <div className="whitespace-pre-line">{statusMessage}</div>
            </div>
          </Card>
        ) : (
          <>
            <div className="mb-8">
              <h2 className="text-xl font-semibold mb-4 text-blue-400">Available Discounts</h2>
              <div className="grid grid-cols-1 md:grid-cols-3 gap-4">
                <Card className="bg-zinc-900 border-0 p-4">
                  <h3 className="font-medium mb-2">Early Bird</h3>
                  <p className="text-sm text-gray-400">15% off before 10 AM</p>
                </Card>
                <Card className="bg-zinc-900 border-0 p-4">
                  <h3 className="font-medium mb-2">Off-Peak Hours</h3>
                  <p className="text-sm text-gray-400">10% off between 2-4 PM</p>
                </Card>
                <Card className="bg-zinc-900 border-0 p-4">
                  <h3 className="font-medium mb-2">Long Duration</h3>
                  <p className="text-sm text-gray-400">20% off for 2+ hours</p>
                </Card>
              </div>
            </div>

            <BookingForm onSubmit={handleBooking} />

            {bookingStatus === "processing" && (
              <div className="mt-4 text-center text-gray-400">
                {statusMessage}
              </div>
            )}

            {bookingStatus === "error" && (
              <div className="mt-4 text-center text-red-500">
                {statusMessage}
              </div>
            )}
          </>
        )}
      </div>
    </div>
  );
}
